                    type: 'Hello'
                }
            }
        })

        // 存取 json 檔案資料, 依照屬性分為不同物件
        let personData = reactive(dataObj.person)
        let schoolData = reactive(dataObj.schools)


        return {
            testObj,
            personData,
            schoolData,
        }
    }
